    <!--添加用户的对话框-->
    <el-dialog
      title="添加宝宝"
      :visible.sync="addDialogVisible"
      width="95%"
      @close="addDialogClosed"
    >
      <!--      内容主体-->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="60px">
        <el-form-item label="姓名" prop="name">
          <el-input v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item label="年龄(月)" prop="age">
          <el-input v-model="addForm.age" type="number" min=0></el-input>
        </el-form-item>
        <el-form-item label="身高" prop="height">
          <el-input v-model="addForm.height" type="number" min=0></el-input>
        </el-form-item>
        <el-form-item label="体重" prop="weight">
          <el-input v-model="addForm.weight" type="number" min=0></el-input>
        </el-form-item>
