<script>
import NavBar from "../../components/navbar/NavBar";
import BetterScroll from "@/components/betterScroll/BetterScroll";
import BackTop from "@/components/backTop/BackTop";
import Father from "@/assets/img/my/father.png";
import Mother from "@/assets/img/my/mother.png";

let Echarts = require('echarts/lib/echarts'); //基础实例 注意不要使用import
require('echarts/charts'); //按需引入
require('echarts/renderers');
import ay from '@/assets/img/my/暗夜.png';
import sc from '@/assets/img/my/收藏.png';
import ls from '@/assets/img/my/历史.png';
import xz from '@/assets/img/my/下载.png';

export default {
  name: "My",
  data() {
    return {
      tabbar: [
        {
          img: ay,
          name: '暗夜'
        },
        {
          img: sc,
          name: '收藏'
        },
        {
          img: ls,
          name: '历史'
        },
        {
          img: xz,
          name: '下载'
        }
      ],
      parentFather: [],
      parentMother: [],
      babyList: [],
      chart: null,
      number: 0.5,
      isShow: false,
      tabOffsetTop: 0,
      username: '',
      addDialogVisible: false,//添加宝宝
      addForm: {
        name: '',
        age: 0,
        height: 0,
        weight: 0,
        bloodType: ''
      },//添加宝宝的表单数据
      addFormRules: {
        name: [{
          required: true,
          message: '请输入宝宝名称',
          trigger: 'blur'
        }],
        age: [{
          required: true,
          message: '请输入年龄',
          trigger: 'blur',
        }],
        height: [{
          required: true,
          message: '请输入身高',
          trigger: 'blur'
        }],
        weight: [{
          required: true,
          message: '请输入体重',
          trigger: 'blur'
        }],
        bloodType: [{
          required: true,
          message: '请选择血型',
          trigger: 'blur'
        }],
      },//添加表单的验证规则对象
      catelist: [
        {
          id: 1,
          name: 'A'
        },
        {
          id: 2,
          name: 'B'
        },
        {
          id: 3,
          name: 'AB'
        },
        {
          id: 4,
          name: 'O'
        },
      ],
      uid: 0
    }
  },
  methods: {
    init() {
      //2.初始化
      this.chart = Echarts.init(this.$refs.chart);
      //3.配置数据
      let option = {
        series: [{
          type: 'gauge',
          startAngle: 180,
          endAngle: 0,
          min: 0,
          max: 1,
          splitNumber: 4,
          axisLine: {
            lineStyle: {
              width: 3,
              color: [
                [0.25, '#7CFFB2'],
                [0.5, '#58D9F9'],
                [0.75, '#FDDD60'],
                [1, '#FF6E76']
              ]
            }
          },
          pointer: {
            icon: 'path://M12.8,0.7l12,40.1H0.7L12.8,0.7z',
            length: '12%',
            width: 15,
            offsetCenter: [0, '-60%'],
            itemStyle: {
              color: 'auto'
            }
          },
          axisTick: {
            length: 10,
            lineStyle: {
              color: 'auto',
              width: 2
            }
          },
          splitLine: {
            length: 15,
            lineStyle: {
              color: 'auto',
              width: 4
            }
          },
          axisLabel: {
            color: '#464646',
            fontSize: 15,
            distance: -60,
            formatter: function (value) {
              if (value === 1) {
                return '高';
              } else if (value === 0.625) {
                return '';
              } else if (value === 0.375) {
                return '';
              } else if (value === 0) {
                return '低';
              }
            }
          },
          title: {
            offsetCenter: [0, '-20%'],
            fontSize: 12,
          },
          detail: {
            fontSize: 12,
            offsetCenter: [0, '0%'],
            valueAnimation: true,
            formatter: function (value) {
              return Math.round(value * 100);
            },
            color: 'auto'
          },
          data: [{
            value: this.number,
            name: 'BMI'
          }]
        }]
      };
      // 4.传入数据
      this.chart.setOption(option);
    },
    contentScroll(position) {
      this.isShow = (-position.y) > 140
      this.isTabFixed = (-position.y) > this.tabOffsetTop
    },
    loadMore() {
      // this.getHomeGoods(this.currentType)
      this.$refs.scroll.refresh()
    },
    backTop() {
      this.$refs.scroll.scrollTo(0, 0)
    },
    // 从后台获取信息
    //父母列表
    parentsList() {
      //获取用户名
      this.username = sessionStorage.getItem('username')
      //  获取父母信息
      this.$http.get('user?id=' + this.uid).then(res => {
        var point = res.data.data.records[0]
        if (!point.parentList){
          this.parentFather = {'role': '爸爸'}
          this.parentMother = {'role': '妈妈'}
        }
        if (point.parentList[0].role === '爸爸') {
          this.parentFather = point.parentList[0]
          if (point.parentList[1]) {
            this.parentMother = point.parentList[1]
          } else {
            this.parentMother = {'role': '妈妈'}
          }
        } else {
          if (point.parentList[1]) {
            this.parentFather = point.parentList[1]
          } else {
            this.parentFather = {'role': '爸爸'}
          }
          this.parentMother = point.parentList[0]
        }
      }).catch(err => err)
    },
    //孩子列表
    childrenList() {
      this.$http.get('user?id=' + this.uid).then(res => {
        var point = res.data.data.records[0]
        //  children
        this.babyList = point.babyList
      }).catch(err => err)
    },
    //  退出登录
    outLogin() {
      //弹框询问用户是否退出
      this.$confirm('确定要退出吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        customClass: 'message-logout'
      }).then(confirmResult => {
        // 清空token
        window.sessionStorage.clear()
        // 重定向到登录页
        this.$router.push('/login')
      }).catch(err => err)
    },
    //  添加宝宝
    //监听添加用户对话框的关闭事件
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    //点击按钮添加新用户
    addUser() {
      this.addForm.age *= 1;
      this.addForm.height *= 1.0;
      this.addForm.weight *= 1.0;
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        this.$set(this.addForm,'userId',this.uid)
        //发起添加用户的网络请求
        const {data: res} = await this.$http.post('baby', this.addForm)
        if (res.status !== 200) {
          this.$message.error('添加宝宝失败')
        }
        this.$message.success('添加宝宝成功')
        //隐藏添加的用户对话框
        this.addDialogVisible = false
        //重新获取用户列表数据（刷新）
        this.parentsList()
        this.childrenList()
        // 清空添加用户对话框
        this.$refs.addFormRef.resetFields()
      })
    },
    //跳转性格调查页面
    toSurvey() {
      this.$router.push('/survey')
    },
    publishArticle() {
      this.$router.push('/publishArticle')
    }
  },
  mounted() {
    this.uid = parseInt(sessionStorage.getItem('id'))
    this.parentsList()
    this.childrenList()
    this.init()
  },
  components: {
    NavBar,
    BetterScroll,
    BackTop
  }
}
</script>

